import React, { useEffect, useState } from 'react';
import BoxShadowForm from "./box-shadow-form";
import Post from "./post";
import BoxPropertyForm from "./box-property-form";
import Header from "./header";

function Index(props) {
    // State to manage box shadow data
    const [boxShadow, setBoxShadow] = useState();

    // State to manage box property data
    const [boxProperty, setBoxProperty] = useState();

    // State to manage the code display status
    const [code, setCode] = useState(false);

    // Function to toggle the code display status
    function showCode() {
        setCode(code?false:true);
    }


    return (
        <>
            {/* Header component with the function to toggle code display */}
            <Header showCode={showCode} />

            <div className={"formContainer"}>
                {/* Component for configuring box shadows */}
                <BoxShadowForm boxShadow={(res) => setBoxShadow(res)} />

                {/* Component for displaying posts and code */}
                <Post closeCode={showCode} showCode={code} boxShadow={boxShadow} boxProperty={boxProperty} />

                {/* Component for configuring box properties */}
                <BoxPropertyForm boxProperty={(res) => setBoxProperty(res)} />
            </div>
        </>
    );
}

export default Index;
