import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Button} from "react-bootstrap";

function Header(props) {
    return (
        <header>
            <div className={"headerContainer"}>
                <div className={'title'}><h1>Box Shadows</h1></div>
                <div className={'headerButtons'}>
                    <a target={"_blank"} href="https://github.com/Arman-sahakyan"><Button variant={"dark"}>GitHub</Button></a>
                    <Button variant={"primary"} onClick={props.showCode}>Show code</Button>
                </div>
            </div>

        </header>
    );
}

export default Header;