import React, { useEffect, useState } from 'react';
import { obj } from "../data";
import { Button } from "react-bootstrap";
import clipboardCopy from "clipboard-copy";

function Post({ boxShadow, boxProperty, showCode, closeCode }) {
    // State to store the generated box shadow style
    const [shadowStyle, setShadowStyle] = useState('');

    // State to store box properties
    const [boxStyle, setBoxStyle] = useState(obj);

    // State to manage the copy status
    const [copy, setCopy] = useState(null);

    // Update box properties when boxProperty changes
    useEffect(() => {
        if (boxProperty) {
            setBoxStyle(boxProperty);
            console.log(boxProperty)
        }

    }, [boxProperty]);

    // Generate box shadow style when boxShadow changes
    useEffect(() => {
        let style = '';
        boxShadow?.map((value, i) => {
            style += value.active ? ((value.inset ? "inset " : '') + value.shadowColor + " " + value.horizontal + 'px ' + value.vertical + 'px ' + value.bloor + 'px ' + value.spread + 'px ' + ((i === boxShadow.length - 1 || !boxShadow[i + 1].active) ? '' : ', ')) : '';
        });
        setShadowStyle(style);
    }, [boxShadow]);

    // Generated code
    const generatedCode = `
            box-shadow: ${shadowStyle}; 
            background: ${boxStyle.background}; 
            border-color: ${boxStyle.borderColor};
            border-radius: ${boxStyle.borderRadius};
            height: ${boxStyle.height};
            width: ${boxStyle.width}; 
            `;

    return (
        <div className={'content'} style={{ background: boxStyle.canvas }}>
            {showCode &&
                <div className={'block'}>
                    <div className={'myModal'}>
                        <h2>Generated Styles</h2>
                        <span>
                            box-shadow: {shadowStyle}; <br />
                            background: {boxStyle.background}; <br />
                            border-color: {boxStyle.borderColor}; <br />
                            border-radius: {boxStyle.borderRadius }; <br />
                            height: {boxStyle.height}; <br />
                            width: {boxStyle.width}; <br />
                        </span>
                        <div className={'modalButtons'}>
                            <Button variant={'secondary'} onClick={() => {
                                clipboardCopy(generatedCode);
                                if (copy) {
                                    setCopy(null);
                                } else {
                                    setCopy(true);
                                    setTimeout(() => { setCopy(null); }, 2500);
                                }
                            }}>{copy ? 'copied!' : 'copy'}</Button>
                            <Button onClick={closeCode}>close</Button>
                        </div>
                    </div>
                </div>
            }
            <div className={'box'} style={{ boxShadow: shadowStyle, width: boxStyle.width, height: boxStyle.height, background: boxStyle.background, borderRadius: boxStyle.borderRadius, border: boxStyle.borderColor + " 10px solid" }}></div>
        </div>
    );
}

export default Post;
