import React, {useEffect, useState} from 'react';
import {inputs} from "../data";
import 'bootstrap/dist/css/bootstrap.min.css';
import {Button} from "react-bootstrap";

// Assuming 'inputs' is defined somewhere, as it's used in the initial state
const BoxShadowForm = ({ boxShadow }) => {
    // State to manage the shadow inputs
    const [state, setState] = useState(inputs);

    // Effect to invoke the 'boxShadow' function whenever the 'state' changes
    useEffect(() => {
        boxShadow(state);
    }, [state]);

    return (
        <div className={'slidebar'}>
            <div className={'addShadow'}>
                <h4>Customize Shadows</h4>
                {/* Button to add a new shadow */}
                <Button variant={"primary"} onClick={() => {
                    setState([...state, {
                        // Initial shadow properties
                        id: Date.now(),
                        show: true,
                        name: "shadow",
                        shadowColor: "#000000",
                        inset: false,
                        active: true,
                        horizontal: '10',
                        vertical: '10',
                        bloor: '10',
                        spread: '10',
                    }])
                }}>
                    Add a Shadow
                </Button>
            </div>

            {/* Mapping through each shadow in the state */}
            {state?.map((value, index) => {
                return (
                    <div key={index}>
                        <div className={'showShadow'} onClick={() => {
                            // Toggle the visibility of the shadow
                            setState(
                                state?.map(val => {
                                    if (val.id === value.id) {
                                        val.show = !val.show;
                                    }
                                    return val;
                                })
                            );
                        }}>
                            {/* Displaying a toggle icon based on the shadow visibility */}
                            <h5>{"shadow " + (index + 1)}</h5>
                            {value.show ?
                                <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 -960 960 960" width="24"><path d="M504-480 320-664l56-56 240 240-240 240-56-56 184-184Z" /></svg> :
                                <svg xmlns="http://www.w3.org/2000/svg" height="24" viewBox="0 -960 960 960" width="24"><path d="M480-345 240-585l56-56 184 184 184-184 56 56-240 240Z" /></svg>
                            }
                        </div>
                        {!value.show && (
                            <div key={index}>
                                {/* Section for editing the shadow properties */}
                                <div className={'inputs'}>
                                    <div className={'space-between'}>
                                        <div className={'inset-active'}>
                                            {/* Checkbox for inset property */}
                                            <input
                                                className={'form-check-input'}
                                                checked={value.inset}
                                                onChange={(e) => {
                                                    setState(
                                                        state?.map(val => {
                                                            if (val.id === value.id) {
                                                                val.inset = e.target.checked;
                                                            }
                                                            return val;
                                                        })
                                                    );
                                                }}
                                                type="checkbox"
                                            />
                                            <label htmlFor="">inset</label>
                                            {/* Checkbox for active property */}
                                            <input
                                                className={'form-check-input'}
                                                checked={value.active}
                                                onChange={(e) => {
                                                    setState(
                                                        state?.map(val => {
                                                            if (val.id === value.id) {
                                                                val.active = e.target.checked;
                                                            }
                                                            return val;
                                                        })
                                                    );
                                                }}
                                                type="checkbox"
                                            />
                                            <label htmlFor="">active</label>
                                        </div>
                                        {/* Button to remove the shadow */}
                                        <Button variant={'danger'} data-id={value.id} onClick={(e) => {
                                            setState(state.filter(val => +val.id !== +e.currentTarget.dataset.id));
                                        }}>remove</Button>
                                    </div>
                                </div>

                                {/* Range input for horizontal shadow property */}
                                <div className={'inputs'}>
                                    <div className={'space-between'}>
                                        <label htmlFor="">horizontal</label>
                                        <span>{value.horizontal + "px"}</span>
                                    </div>
                                    <input
                                        className={'form-range'}
                                        max={100}
                                        min={-100}
                                        defaultValue={value.horizontal}
                                        type="range"
                                        onChange={(e) => {
                                            setState(
                                                state?.map(val => {
                                                    if (val.id === value.id) {
                                                        val.horizontal = e.target.value;
                                                    }
                                                    return val;
                                                })
                                            );
                                        }}
                                    />
                                </div>

                                {/* Range input for vertical shadow property */}
                                <div className={'inputs'}>
                                    <div className={'space-between'}>
                                        <label htmlFor="">vertical</label>
                                        <span>{value.vertical + "px"}</span>
                                    </div>
                                    <input
                                        className={'form-range'}
                                        max={100}
                                        min={-100}
                                        defaultValue={value.vertical}
                                        type="range"
                                        onChange={(e) => {
                                            setState(
                                                state?.map(val => {
                                                    if (val.id === value.id) {
                                                        val.vertical = e.target.value;
                                                    }
                                                    return val;
                                                })
                                            );
                                        }}
                                    />
                                </div>

                                {/* Range input for blur shadow property */}
                                <div className={'inputs'}>
                                    <div className={'space-between'}>
                                        <label htmlFor="">blur</label>
                                        <span>{value.bloor + "px"}</span>
                                    </div>
                                    <input
                                        className={'form-range'}
                                        max={100}
                                        min={0}
                                        defaultValue={value.bloor}
                                        type="range"
                                        onChange={(e) => {
                                            setState(
                                                state?.map(val => {
                                                    if (val.id === value.id) {
                                                        val.bloor = e.target.value;
                                                    }
                                                    return val;
                                                })
                                            );
                                        }}
                                    />
                                </div>

                                {/* Range input for spread shadow property */}
                                <div className={'inputs'}>
                                    <div className={'space-between'}>
                                        <label htmlFor="">spread</label>
                                        <span>{value.spread + "px"}</span>
                                    </div>
                                    <input
                                        className={'form-range'}
                                        max={100}
                                        min={-100}
                                        defaultValue={value.spread}
                                        type="range"
                                        onChange={(e) => {
                                            setState(
                                                state?.map(val => {
                                                    if (val.id === value.id) {
                                                        val.spread = e.target.value;
                                                    }
                                                    return val;
                                                })
                                            );
                                        }}
                                    />
                                </div>

                                {/* Input for shadow color */}
                                <div className={'inputs'}>
                                    <label htmlFor="">color</label>
                                    <div className={'space-between'}>
                                        <input
                                            onChange={(e) => {
                                                setState(
                                                    state?.map(val => {
                                                        if (val.id === value.id) {
                                                            val.shadowColor = e.target.value;
                                                        }
                                                        return val;
                                                    })
                                                );
                                            }}
                                            value={value.shadowColor} className={'textInput'} type="text" />
                                        {/* Color picker for shadow color */}
                                        <input
                                            onChange={(e) => {
                                                setState(
                                                    state?.map(val => {
                                                        if (val.id === value.id) {
                                                            val.shadowColor = e.target.value;
                                                        }
                                                        return val;
                                                    })
                                                );
                                            }} type="color" />
                                    </div>
                                </div>
                            </div>
                        )}
                    </div>
                );
            })}
        </div>
    );
}



export default BoxShadowForm;

