import React, { useEffect, useState } from 'react';
import { obj } from "../data";

function BoxPropertyForm({ boxProperty }) {
    // State to manage box properties
    const [state, setState] = useState(obj);

    // Effect to invoke the 'boxProperty' function whenever the 'state' changes
    useEffect(() => {
        boxProperty(state);
    }, [state]);

    return (
        <div className={'shadowForm'}>
            <h3>Box properties</h3>
            <div>
                {/* Input for Canvas color */}
                <div className={'inputs'}>
                    <label htmlFor="">Canvas color</label>
                    <input
                        className={'textInput'}
                        onChange={(e) => {
                            const canvas= e.currentTarget.value
                            setState(res=> {
                                    return {
                                        ...res,
                                        canvas: canvas
                                    }
                                }
                            );
                        }}
                        defaultValue={'#f3f4f6'} type="text" />
                </div>

                {/* Input for Background color */}
                <div className={'inputs'}>
                    <label htmlFor="">Background color</label>
                    <input
                        className={'textInput'}
                        onChange={(e) => {
                            const background=e.currentTarget.value
                            setState(res=> {
                                    return {
                                        ...res,
                                        background: background
                                    }
                                }
                            );
                        }}
                        defaultValue={'#fff'} type="text" />
                </div>

                {/* Input for Border color */}
                <div className={'inputs'}>
                    <label htmlFor="">Border color</label>
                    <input
                        className={'textInput'}
                        onChange={(e) => {
                            const borderColor=e.currentTarget.value
                            setState(res=> {
                                    return {
                                        ...res,
                                        borderColor: borderColor
                                    }
                                }
                            );
                        }}
                        defaultValue={'transparent'} type="text" />
                </div>

                {/* Input and range for Border radius */}
                <div className={'inputs'}>
                    <div className={'space-between'}>
                        <label htmlFor="">Border radius</label>
                        <span>{state.borderRadius}</span>
                    </div>
                    <input
                        className={'form-range'}
                        onChange={(e) => {
                            const borderRadius=e.currentTarget.value
                            setState(res=> {
                                    return {
                                        ...res,
                                        borderRadius: borderRadius+'rem'
                                    }
                                }
                            );
                        }}
                        min={0} max={20} defaultValue={2} type="range" />
                </div>

                {/* Input and range for Height */}
                <div className={'inputs'}>
                    <div className={'space-between'}>
                        <label htmlFor="">Height</label>
                        <span>{state.height}</span>
                    </div>
                    <input
                        className={'form-range'}
                        onChange={(e) => {
                            const height=e.currentTarget.value
                            setState(res=> {
                                    return {
                                        ...res,
                                        height: height + "rem"
                                    }
                                }
                            );
                        }}
                        min={0} max={40} defaultValue={20} type="range" />
                </div>

                {/* Input and range for Width */}
                <div className={'inputs'}>
                    <div className={'space-between'}>
                        <label htmlFor="">Width</label>
                        <span>{state.width}</span>
                    </div>
                    <input
                        className={'form-range'}
                        onChange={(e) => {
                            const width=e.currentTarget.value
                            setState(res=> {
                                    return {
                                        ...res,
                                        width: width + 'rem'
                                    }
                                }
                            );
                        }}
                        min={0} max={40} defaultValue={20} type="range" />
                </div>
            </div>
        </div>
    );
}

export default BoxPropertyForm;
